<template>
  <div class="modal" v-if="modalVisible" tabindex="0" @keyup.esc="toggleModal">
    <transition appear enter-active-class="fadeIn" leave-active-class="fadeOut">
      <div class="modal__content" v-if="modalName">
        <component v-bind="modal.props" :is="modalName"/>
      </div>
    </transition>
  </div>
</template>

<script>
import * as modals from "@/components/modal/index";
import { mapState, mapMutations } from "vuex";

export default {
  name: "AppModal",
  components: { ...modals },
  computed: {
    ...mapState(["modal", "modalVisible"]),
    modalName() {
      const { name } = this.modal;
      if (!name || !this.$options.components[name]) {
        console.warn(`${name} Modal not found`);
        return null;
      }
      return name;
    }
  },
  methods: {
    ...mapMutations(["toggleModal"])
  },
  watch: {
    modalVisible(state) {
      // if (state && !document.fullscreenElement) {
      //   document.documentElement.requestFullscreen();
      // }
      // if (
      //   document.fullscreenElement ||
      //   document.webkitFullscreenElement ||
      //   document.mozFullScreenElement
      // ) {
      //   document.exitFullscreen();
      // }
    }
  }
};
</script>

