<template>
  <div class="profile-modal" v-hammer:swipe.all="onSwipe">
    <div class="story">
      <Close class="story-modal__close"/>
      <header class="story__header">
        <Progress :count="storyItems.length" :time="time" :activeNumber="activeStoryItemIndex + 1"/>
        <div class="story__info">
          <CircleImg :src="user.avatar" :title="user.title"/>
          <span class="story__title">Hikayen</span>
        </div>
      </header>
      <Media
        v-if="activeItem"
        :type="activeItem.type"
        :src="activeItem.mediaUrl"
        @load="loadMedia"
      />
      <footer class="profile-modal__footer">
        <span>
          <Icon name="Group"/>
          {{ activeItem.viewingUsers ? activeItem.viewingUsers.length : 0 }} Kii grd
        </span>
        <Button class="-position-right" @click="deleteStory(activeItem.id)">
          <Icon name="Bin"/>
        </Button>
      </footer>
    </div>
  </div>
</template>

<script>
import { mapGetters, mapMutations } from "vuex";
export default {
  name: "Profile",
  data() {
    return {
      time: 0,
      stop: false,
      loading: true
    };
  },
  computed: {
    ...mapGetters({
      user: "user",
      activeStoryItemIndex: "activeStoryItemIndex"
    }),
    storyItems() {
      if (!this.user) return [];
      return this.user.stories;
    },
    activeItem() {
      return this.storyItems[this.activeStoryItemIndex];
    }
  },
  methods: {
    ...mapMutations([
      "setActiveStoryItemIndex",
      "storyItemDelete",
      "toggleModal"
    ]),
    loadMedia() {
      this.time = 0;
      this.timer();
    },
    timer() {
      this.loading = false;
      let currentTime = 0;
      const time = this.activeItem.duration || 5;

      const interVal = setInterval(() => {
        if (this.stop === true) {
          clearInterval(interVal);
        }
        if (currentTime >= time) {
          clearInterval(interVal);
          if (this.activeStoryItemIndex < this.storyItems.length - 1) {
            this.time = 0;
            this.loading = true;
            this.setActiveStoryItemIndex(this.activeStoryItemIndex + 1);
          }
        } else {
          currentTime += 1;
          this.time = (currentTime / time) * 100;
        }
      }, 1000);
    },
    onSwipe(e) {
      switch (e.type) {
        case "swipeleft":
          this.swipeLeft();
          break;
        case "swiperight":
          this.swipeRight();
          break;
      }
    },
    swipeRight() {
      if (this.activeStoryItemIndex > 0) {
        this.setActiveStoryItemIndex(this.activeStoryItemIndex - 1);
      }
    },
    swipeLeft() {
      if (this.activeStoryItemIndex < this.storyItems.length - 1) {
        this.setActiveStoryItemIndex(this.activeStoryItemIndex + 1);
      }
    },
    deleteStory(id) {
      if (this.storyItems.length === 1) {
        this.toggleModal(() => {
          this.storyItemDelete(id);
        });
      } else {
        this.storyItemDelete(id);
      }
    }
  },
  async mounted() {
    await this.$nextTick();
  }
};
</script>

<style lang="scss">
.profile-modal {
  width: 100%;
  height: 100%;
  color: #fff;

  &__footer {
    display: flex;
    align-items: center;
    color: #fff;
    padding: 15px;
  }
}
</style>