<template>
  <Tab :tabs="tabs" @changeTab="changeType">
    <!-- Tab Header -->
    <template slot="header">
      <OptionsBar/>
    </template>
    <!-- Tab Content -->
    <canvas id="canvas"/>
    <Media :src="file.src" ref="media" :type="file.type"/>
    <CanvasText
      v-if="optionIndex === 1"
      v-model="text"
      :placeholder="placeHolder"
      @focus="focus = true"
      @blur="focus = false"
      :font="selectedFont.font"
      :color="selectedColor"
      ref="text"
    />
    <Emoji @selectEmoji="selectEmoji"/>
    <EmojiCreate ref="emojiItem" :emoji="selectedEmoji" @position="setEmojiCanvasPosition"/>
    <!-- Tab Footer -->
    <template slot="footer">
      <ColorSetting/>
      <ActionsBar>
        <Button @click.native="reply" class="-position-center" column>
          <CircleImg :src="story.avatar" :title="story.title"/>
          <span>{{story.title}}</span>
        </Button>
      </ActionsBar>
    </template>
  </Tab>
</template>

<script>
import { mapMutations, mapGetters } from "vuex";
import drawCanvas from "@/mixins/drawCanvas";

export default {
  name: "ReplyStory",
  mixins: [drawCanvas({ onLoad: false })],
  props: {
    media: null,
    story: null
  },
  data() {
    return {
      tabs: ["TEKRAR OYNAT", "BR KEZ GRNTLE"],
      currentTab: 0,
      modes: ["Text", "Smile"],
      currentMode: 0,
      selectedText: "Yazmak iin tkla",
      focusText: "Bireyler yaz",
      selectedEmoji: null,
      focus: false,
      text: null,
      file: {
        type: "image",
        src: "img/story.png"
      }
    };
  },
  computed: {
    ...mapGetters({
      optionIndex: "activeOption",
      activeFile: "activeFile"
    }),
    placeHolder() {
      return this.focus ? this.focusText : this.selectedText;
    }
  },
  watch: {
    media(files) {
      this.fileChange(files);
    },
    activeFile(file) {
      if (file) {
        this.fileChange(file);
      }
    }
  },
  methods: {
    ...mapMutations([
      "sendMessage",
      "setActiveStory",
      "changeModal",
      "toggleEmoji"
    ]),
    setEmojiCanvasPosition() {},
    selectEmoji(e) {
      this.toggleEmoji();
      this.selectedEmoji = e;
    },
    async reply() {
      const res = await this.drawItems();
      const result = await this.sendMessage({
        type: this.tabs[this.currentTab],
        media: result
      });

      this.setActiveStory(this.story.id);
      this.changeModal({
        name: "Story"
      });
    },
    changeType(index) {
      this.currentTab = index;
    },
    fileChange(files) {
      const file = files[0];
      if (file.type.match("image.*|video.*")) {
        const reader = new FileReader();
        reader.readAsDataURL(file);
        reader.onload = evt => {
          if (evt.target.readyState == FileReader.DONE) {
            console.log("ds");
            this.file = {
              src: evt.target.result,
              type: file.type.match("image.*") ? "image" : "video"
            };
          }
        };
      }
    },
    drawItems() {
      const media = this.$refs.media.$el;
      const text = this.$refs.text;
      const emoji = this.$refs.emojiItem;

      let items = {};

      if (text) {
        items = {
          ...items,
          text: {
            data: this.text,
            x: text.coordinat.x,
            y: text.coordinat.y,
            font: text.font,
            color: text.color
          }
        };
      }

      if (emoji) {
        items = {
          ...items
        };
      }

      console;
      return this.draw({
        media: this.file,
        mediaData: media,
        items
      });
    }
  }
};
</script>

<style lang="scss" scoped>
#canvas {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  object-fit: cover;
  z-index: -1;
}
</style>