<template>
  <Tab :tabs="tabs" @changeTab="changeType">
    <!-- Tab Header -->
    <template slot="header">
      <OptionsBar/>
    </template>
    <!-- Tab Content -->
    <canvas id="canvas"/>
    <TextMode v-if="currentTab === 0"/>
    <NormalMode v-if="currentTab === 1"/>
    <!-- Tab Footer -->
    <template slot="footer">
      <ColorSetting/>
      <ActionsBar @action="action"/>
    </template>
  </Tab>
</template>

<script>
import { mapMutations, mapGetters } from "vuex";

export default {
  name: "NewStory",
  computed: {
    ...mapGetters({
      tabs: "types",
      currentTab: "activeType",
      optionIndex: "activeOption",
      actionFunc: "actionFunc"
    })
  },
  methods: {
    ...mapMutations(["updateUser", "changeType", "toggleModal"]),
    action() {
      if (typeof this.actionFunc === "function") {
        const result = this.actionFunc();
        this.updateUser({
          type: result.type,
          mediaUrl: result.data,
          viewingUsers: [],
          duration: result.duration || 5
        });
        this.toggleModal();
      }
    }
  }
};
</script>

<style lang="scss">
#canvas {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  object-fit: cover;
  z-index: 1;
}
</style>