<template>
  <div class="action-bar">
    <div class="action-bar__left">
      <Button v-if="activeIndex === 0" type="circle" @click="changeBackground">
        <canvas id="background-view"></canvas>
      </Button>
      <File
        v-if="activeIndex === 1"
        multiple
        accept="file_extension|video/*|image/*"
        @change="fileChangeHandler"
      />
      <slot name="left"/>
    </div>

    <Button type="create" @click="$emit('action')" v-if="!$slots.default">
      <Icon name="Right"/>
    </Button>
    <slot $slots.default/>

    <div v-if="$slots.right" class="action-bar__right">
      <slot name="right"/>
    </div>
  </div>
</template>

<script>
import { mapMutations, mapGetters } from "vuex";

export default {
  name: "ActionsBar",
  computed: {
    ...mapGetters({
      activeIndex: "activeType"
    }),
    btnActionVariants() {
      return {
        "-radius": this.activeIndex === 1
      };
    }
  },
  methods: {
    ...mapMutations(["changeActiveFile"]),
    fileChangeHandler(files) {
      this.changeActiveFile(files);
    }
  }
};
</script>

<style lang="scss">
.action-bar {
  display: flex;
  align-items: center;
  position: relative;
  height: 100px;
  padding: 0px 10px;
}

#background-view {
  width: 20px;
  height: 20px;
  border-radius: 10px;
}
</style>