<template>
  <div class="text-mode">
    <CanvasText
      v-model="text"
      :placeholder="placeHolder"
      :font="selectedFont.font"
      :color="selectedColor"
      ref="text"
    />
    <Emoji @selectEmoji="selectEmoji"/>
    <EmojiCreate ref="emojiItem" :emoji="selectedEmoji" @position="setEmojiCanvasPosition"/>
  </div>
</template>

<script>
import drawCanvas from "@/mixins/drawCanvas";
import { mapMutations } from "vuex";

export default {
  name: "TextMode",
  mixins: [
    drawCanvas({
      mode: "text"
    })
  ],
  data() {
    return {
      selectedText: "Yazmak iin tkla",
      focusText: "Bireyler yaz",
      focus: false,
      text: "",
      selectedEmoji: null
    };
  },
  computed: {
    placeHolder() {
      return this.focus ? this.focusText : this.selectedText;
    }
  },
  methods: {
    ...mapMutations(["toggleEmoji", "setActionFunc"]),
    setEmojiCanvasPosition() {},
    selectEmoji(e) {
      this.toggleEmoji();
      this.selectedEmoji = e;
    },
    drawItems() {
      const text = this.$refs.text;
      const emoji = this.$refs.emojiItem;

      let media = {
        type: "bg"
      };

      let items = {};

      if (text) {
        items = {
          ...items,
          text: {
            data: this.text,
            x: text.coordinat.x,
            y: text.coordinat.y,
            font: text.font,
            color: text.color
          }
        };
      }

      if (emoji) {
        items = {
          ...items,
          emoji: {
            data: this.selectedEmoji,
            x: emoji.coordinat.x,
            y: emoji.coordinat.y
          }
        };
      }

      return this.draw({
        media,
        items
      });
    }
  },
  mounted() {
    this.setActionFunc(this.drawItems);
  }
};
</script>

<style lang="scss">
.text-mode {
  width: 100%;
  height: 100%;
}
</style>