<template>
  <transition-group
    v-if="optionIndex === 1"
    tag="div"
    class="color-settings"
    appear
    enter-active-class="bounceIn"
    leave-active-class="bounceOut"
  >
    <Button
      type="color"
      v-for="(color, i) in settings.colors"
      :key="color"
      :style="{ backgroundColor: color }"
      @click.prevent="changeColor(i)"
    >
      <Icon v-if="i === settings.activeColor" name="Paint" fill="gray"/>
    </Button>
  </transition-group>
</template>

<script>
import { mapGetters } from "vuex";

export default {
  name: "ColorSettings",
  computed: {
    ...mapGetters({
      optionIndex: "activeOption"
    })
  }
};
</script>

<style lang="scss">
.color-settings {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
}
</style>