<template>
  <div class="emoji-create" v-hammer:pan.all="onPan">{{emoji}}</div>
</template>

<script>
export default {
  name: "EmojiCreate",
  props: {
    emoji: {
      type: String
    }
  },
  data() {
    return {
      lastX: 0,
      lastY: 0,
      isDragging: false,
      coordinat: {
        x: 0,
        y: 0
      }
    };
  },
  methods: {
    onPan(ev) {
      const elem = ev.target;

      if (!this.isDragging) {
        this.isDragging = true;
        this.lastX = elem.offsetLeft;
        this.lastY = elem.offsetTop;
        elem.style.transform = "none";
      }

      const posX = ev.deltaX + this.lastX;
      const posY = ev.deltaY + this.lastY;
      elem.style.left = `${posX}px`;
      elem.style.top = `${posY}px`;

      this.coordinat = {
        x: posX,
        y: posY
      };
    }
  }
};
</script>

<style lang="scss">
.emoji-create {
  position: fixed;
  left: 50%;
  top: 50%;
  cursor: move;
  transform: translate(-50%, -50%);
  z-index: 999;
  font-size: 40px;
  width: 50px;
  height: 50px;
}
</style>
