<template>
  <div class="normal-mode">
    <Media :src="file.src" ref="media" :type="file.type"/>
    <CanvasText
      v-if="optionIndex === 1"
      v-model="text"
      :placeholder="selectedText"
      :font="selectedFont.font"
      :color="selectedColor"
      bg="#162556"
      ref="text"
    />
    <Emoji @selectEmoji="selectEmoji"/>
    <EmojiCreate ref="emojiItem" :emoji="selectedEmoji" @position="setEmojiCanvasPosition"/>
  </div>
</template>

<script>
import { mapGetters, mapMutations } from "vuex";
import drawCanvas from "@/mixins/drawCanvas";

export default {
  name: "NormalMode",
  mixins: [
    drawCanvas({
      onLoad: false
    })
  ],
  data() {
    return {
      selectedText: "Yazmak iin tkla",
      selectedEmoji: null,
      file: {
        type: "image",
        src: "img/story.png"
      },
      text: null
    };
  },
  computed: {
    ...mapGetters({
      activeFile: "activeFile",
      optionIndex: "activeOption"
    })
  },
  watch: {
    activeFile(file) {
      if (file) {
        this.fileChange(file);
      }
    }
  },
  methods: {
    ...mapMutations(["toggleEmoji", "setActionFunc"]),
    setEmojiCanvasPosition() {},
    selectEmoji(e) {
      this.toggleEmoji();
      this.selectedEmoji = e;
    },
    fileChange(files) {
      const file = files[0];
      if (file.type.match("image.*|video.*")) {
        const reader = new FileReader();
        reader.readAsDataURL(file);
        reader.onload = evt => {
          if (evt.target.readyState == FileReader.DONE) {
            this.file = {
              src: evt.target.result,
              type: file.type.match("image.*") ? "image" : "video"
            };
          }
        };
      }
    },
    drawItems() {
      if (this.file.type === "video") {
        return {
          type: "video",
          data: this.file.src,
          duration: this.$refs.media.$el.duration
        };
      }
      const media = this.$refs.media.$el;
      const text = this.$refs.text;
      const emoji = this.$refs.emojiItem;

      let items = {};

      if (text) {
        items = {
          ...items,
          text: {
            data: this.text,
            x: text.coordinat.x,
            y: text.coordinat.y,
            font: text.font,
            color: text.color
          }
        };
      }

      if (emoji) {
        items = {
          ...items
        };
      }

      return this.draw({
        media: this.file,
        mediaData: media,
        items
      });
    }
  },
  mounted() {
    this.setActionFunc(this.drawItems);
  }
};
</script>

<style lang="scss">
.normal-mode {
  width: 100%;
  height: 100%;
}
</style>