<template>
  <div class="story" @click="onClick">
    <header class="story__header" v-show="!full">
      <Progress
        :count="storyItems.length"
        :time="progressTime"
        :activeNumber="activeProgressIndex"
      />
      <div class="story__info">
        <CircleImg :src="storyData.avatar" :title="storyData.title"/>
        <span class="story__title">{{ storyData.title }}</span>
      </div>
    </header>
    <Emoji title="Hzl Cevap" @selectEmoji="sendEmoji"/>
    <Media v-if="activeItem" :type="activeItem.type" :src="activeItem.mediaUrl" @load="loadMedia"/>
    <footer class="story__footer" v-show="!full">
      <Button type="circle" size="md" class="-m-right-10">
        <File accept="image/*" @change="replyPhoto">
          <Icon name="Camera"/>
        </File>
      </Button>
      <InputText
        placeholder="Mesaj Gnder"
        v-model="message"
        @keyup.enter="messageHandler"
        @focus="() => focus = true"
        @blur="() => focus = false"
      >
        <Button>
          <Icon name="More"/>
        </Button>
      </InputText>
    </footer>
  </div>
</template>

<script>
import { mapMutations, mapGetters } from "vuex";

export default {
  name: "Story",
  props: {
    storyData: {
      type: Object,
      required: true
    },
    pause: {
      type: Boolean,
      default: false
    }
  },
  data() {
    return {
      activeProgressIndex: 1,
      currentTime: 0,
      message: "",
      focus: false,
      mediaLoaded: false
    };
  },
  computed: {
    ...mapGetters({
      activeStory: "activeStory",
      activeStoryIndex: "activeStoryIndex",
      activeStoryItemIndex: "activeStoryItemIndex",
      activeUser: "user",
      emojiVisible: "emojiVisible"
    }),
    progressTime() {
      if (!this.activeItem) return 0;
      const time = this.activeItem.duration || 10;
      return (this.currentTime / time) * 100;
    },
    stop() {
      return !this.mediaLoaded || this.focus || this.emojiVisible || this.pause;
    },
    full() {
      return this.pause && (!this.focus || !this.emojiVisible);
    },
    storyItems() {
      return this.storyData.stories;
    },
    activeItem() {
      return this.storyItems[this.activeProgressIndex - 1];
    }
  },
  watch: {
    stop(status) {
      if (!status) {
        this.timer();
      }
    },
    focus() {
      if (this.message.length === 0) {
        this.toggleEmoji();
      }
    },
    message() {
      if (this.message.length > 0) {
        this.closeEmoji();
      } else {
        this.toggleEmoji();
      }
    },
    activeProgressIndex(index) {
      if (index > this.storyItems.length) {
        this.goNextStory();
      }
      if (index < 0) {
        this.goPrevStory();
      }
    }
  },
  methods: {
    ...mapMutations([
      "changeModal",
      "setActiveStoryItemIndex",
      "toggleEmoji",
      "closeEmoji",
      "sendMessage",
      "setViewUser",
      "goNextStory",
      "goPrevStory"
    ]),
    onClick(e) {
      this.time = 0;
      const screenWidth = this.$el.clientWidth / 2;
      if (e.x >= screenWidth) {
        this.activeProgressIndex += 1;
      } else {
        this.activeProgressIndex -= 1;
      }
    },
    loadMedia() {
      this.mediaLoaded = true;
    },
    timer() {
      const progress = () => {
        if (this.progressTime >= 100) {
          clearInterval(interval);
          if (this.activeProgressIndex < this.storyItems.length) {
            this.mediaLoaded = false;
            this.activeProgressIndex += 1;
            this.currentTime = 0;
            this.timer();
            this.viewUser();
          }
        }
        if (this.stop) {
          clearInterval(interval);
        } else {
          this.currentTime += 1;
        }
      };
      const interval = setInterval(progress, 1000);
    },
    viewUser() {
      this.setViewUser({
        id: this.storyData.id,
        index: this.activeProgressIndex - 1
      });
    },
    sendEmoji(emoji) {
      this.sendMessage({
        message: emoji,
        story: this.storyData,
        storyMedia: this.storyItems[this.activeProgressIndex - 1],
        sender: this.activeUser
      });
      this.toggleEmoji();
    },
    messageHandler() {
      this.sendMessage({
        message: this.message,
        story: this.storyData,
        storyMedia: this.storyItems[this.activeProgressIndex - 1],
        sender: this.activeUser
      });
    },
    replyPhoto(files) {
      const file = files[0];
      this.changeModal({
        name: "Reply",
        props: {
          media: file,
          story: this.storyData
        }
      });
    }
  },
  mounted() {
    this.activeProgressIndex = 1;
    this.time = 0;
    this.timer();
    this.viewUser();
    console.log("ds");
  }
};
</script>


