export default (params = {}) => {
  return {
    data() {
      return {
        canvas: null,
        context: null,
        width: 0,
        height: 0
      };
    },
    methods: {
      draw(items) {
        if (items.media) {
          if (items.media.type === "image") {
            this.drawImg(items.mediaData);
          } else if (items.media.type === "video") {
            this.drawVideo(items.mediaData);
          } else if (items.media.type === "bg") {
            this.drawBg();
          }
          Object.entries(items.items).forEach(item => {
            const key = item[0];
            const val = item[1];
            if (key === "text" && val.data) {
              const { data, x, y, font, color } = val;
              this.drawText(data || "", x, y, font, color);
            }
            if (key === "emoji" && val.data) {
              const { data, x, y } = val;
              this.drawEmoji(data, x, y);
            }
          });
          const media = this.canvas.toDataURL("image/png");
          return {
            type: "image",
            data: media
          };
        }
      },
      drawEmoji(emoji, x, y) {
        this.context.save();
        this.context.beginPath();
        this.context.font = "40px Arial";
        this.context.fillText(emoji, x, y);
        this.context.restore();
      },
      drawVideo(video) {
        if (!video || video.paused || !this.context) return false;
        this.context.drawImage(video, 0, 0, this.width, this.height);
        this.context.restore();
        setTimeout(this.drawVideo.bind(this, video), 20); // FPS
      },
      drawImg(image) {
        if (!this.context) return false;
        this.context.drawImage(image, 0, 0, this.width, this.height);
        this.context.restore();
      },
      drawBg() {
        this.currentMode = "drawBg";
        this.clearContext();
        const { position, colors } = this.selectedBackground;

        const grd = this.context.createLinearGradient.apply(
          this.context,
          position
        );
        colors.forEach(c => {
          grd.addColorStop(c.position, c.color);
        });

        this.context.fillStyle = grd;
        this.context.fillRect(0, 0, this.width, this.height);
        this.context.restore();
      },
      drawText(text, x, y, font, color) {
        this.context.save();
        this.context.font = font;
        this.context.fillStyle = color;
        this.context.strokeStyle = color;
        this.context.textAlign = "center";
        this.context.textBaseline = "middle";
        const lineheight = 40;
        const lines = text.split("\n");
        for (var i = 0; i < lines.length; i++) {
          if (this.selectedFont.stroke) {
            this.context.strokeText(
              lines[i],
              this.width / 2,
              y + i * lineheight,
              this.width - 100
            );
          } else {
            this.context.fillText(
              lines[i],
              this.width / 2,
              y + i * lineheight,
              this.width - 100
            );
          }
        }
        this.context.restore();
      },
      clearContext() {
        this.context.setTransform(1, 0, 0, 1, 0, 0);
        this.context.clearRect(0, 0, this.canvas.width, this.canvas.height);
        this.context.restore();
      },
      backgroundView() {
        const canvas = document.getElementById("background-view");
        if (!canvas) return false;
        canvas.width = 20;
        canvas.height = 20;
        const context = canvas.getContext("2d");
        const centerX = canvas.width / 2;
        const centerY = canvas.height / 2;
        const radius = canvas.width / 2;

        context.beginPath();
        context.arc(centerX, centerY, radius, 0, 2 * Math.PI, false);
        const { position, colors } = this.selectedBackground;

        const grd = context.createLinearGradient.apply(context, position);
        colors.forEach(c => {
          grd.addColorStop(c.position, c.color);
        });
        context.fillStyle = grd;
        context.fill();
      },
      initCanvas() {
        this.canvas = document.getElementById("canvas");
        this.canvas.width = this.width;
        this.canvas.height = this.height;
        this.context = this.canvas.getContext("2d");
        if (params.onLoad !== false) {
          this.drawBg();
        }
      },
      init() {
        this.width = this.$el.clientWidth;
        this.height = this.$el.clientHeight;
        this.backgroundView();
        this.initCanvas();
      }
    },
    async mounted() {
      await this.$nextTick();
      this.init();
    }
  };
};
